#hero{
  position: relative;
}


.heroTextContainer{
  position: absolute;
  padding: 40px;
  width: 435px;
  margin-top: 50px;
  height: -webkit-fill-available;
}
* {
  font-family: 'Nunito', sans-serif;
}

#designsContainer{
  position: relative;
  z-index: -1;
}

.heroTitle {
  color: #072e62;
  font-size: 4em;
  font-weight: bold;
}

.heroSubTitle{
  color: #0f3b76;
  font-size: 2em;
  margin-bottom: 75px;
}

body{
  margin: 0;
  background-color: #fcf2ff;
}
#fullDesign{
  width: -webkit-fill-available;
}

#getStarted {
  background: #B60BE6;
  background: -webkit-linear-gradient(right, #B60BE6, #CC6CE6);
  background: -moz-linear-gradient(right, #B60BE6, #CC6CE6);
  background: linear-gradient(to left, #B60BE6, #CC6CE6);
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 40px;
    color: #FFFFFF;
    font-family: 'Nunito', sans-serif;
    font-size: 1em;
    font-weight: 700;
    padding: 20px;
    /* -webkit-box-shadow: 1px 1px 20px 0 #000000; */
    /* -moz-box-shadow: 1px 1px 20px 0 #000000; */
    /* box-shadow: 1px 1px 20px 0 #000000; */
    text-shadow: 1px 1px 20px #000000;
    text-decoration: none;
    /* display: inline-block; */
    cursor: pointer;
    text-align: center;
 }
 
#getStarted:hover {
    border: solid #337FED 0;
    background: #5B0573;
    background-image: -webkit-linear-gradient(top, #5B0573, #751890);
    background-image: -moz-linear-gradient(top, #5B0573, #751890);
    background-image: -ms-linear-gradient(top, #5B0573, #751890);
    background-image: -o-linear-gradient(top, #5B0573, #751890);
    background-image: -webkit-gradient(to bottom, #5B0573, #751890);
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 40px;
    text-decoration: none;
 }

 #logo{
   width: 75px;
 }